.hero {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 4em;
  position: relative;
}

.hero .hero-left {
  width: 100%;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding: 5em 0;
}

.hero .hero-left .hero-title {
  margin: .8em 0;
  text-align: left;
  font-size: 3em;
  line-height: 1.5em;
}

.hero .hero-left .hero-text {
  margin: .8em 0;
  text-align: left;
  font-size: 18px;
  font-weight: 400;
  width: 79%;
  line-height: 1.5em;
}

.hero .hero-left .hero-button-div {
  margin: .8em 0;
}

.hero .hero-left .hero-button-div .hero-button {
  padding: 1.2em 1.3em;
  margin: 1em 1em 0 0;
  color: white;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;
  -webkit-box-shadow: 0 10px 20px #eee, 0 10px 10px #eee;
          box-shadow: 0 10px 20px #eee, 0 10px 10px #eee;
}

.hero .hero-left .hero-button-div .blue {
  background-color: #5368df;
  border: 2px solid #5368df;
}

.hero .hero-left .hero-button-div .blue:hover {
  background-color: white;
  color: #5368df;
  border: 2px solid #5368df;
}

.hero .hero-left .hero-button-div .grey {
  color: black;
  border: 2px solid #eee;
}

.hero .hero-left .hero-button-div .grey:hover {
  background-color: white;
  color: black;
  border: 2px solid black;
}

.hero .hero-left .hero-button-div:hover {
  background-color: white;
}

.hero .hero-right {
  width: 100%;
  padding: 4em 0;
}

.hero .hero-right .background-blue {
  background-color: #5368df;
  position: absolute;
  width: 40rem;
  height: 22.5rem;
  right: 0;
  bottom: 100px;
  z-index: -1;
  border-radius: 0 0 0 50%;
}

@media screen and (max-width: 500px) {
  .hero {
    -webkit-box-orient: vertical;
    -webkit-box-direction: reverse;
        -ms-flex-direction: column-reverse;
            flex-direction: column-reverse;
    padding: 0;
    width: 100%;
  }
  .hero .hero-right img {
    width: 100%;
  }
  .hero .hero-right .background-blue {
    bottom: 550px;
    right: -250px;
  }
  .hero .hero-left {
    text-align: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
  }
  .hero .hero-left .hero-title {
    text-align: center;
  }
  .hero .hero-left .hero-text {
    text-align: center;
    width: 100%;
    padding: 0 2em;
    line-height: 2em;
  }
}

@media screen and (max-width: 425px) {
  .hero .hero-right .background-blue {
    top: 100px;
    right: -400px;
  }
  .hero .hero-left .hero-button-div {
    margin: 0 auto;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: space-evenly;
        -ms-flex-pack: space-evenly;
            justify-content: space-evenly;
    padding-top: 1em;
  }
  .hero .hero-left .hero-button-div .hero-button {
    margin: 0;
  }
}
